import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Key } from 'protractor';
import { ISubcategory } from 'src/app/shared/interfaces/i-category';
import { IService } from 'src/app/shared/interfaces/i-service';
import { NotificationService } from '../../../services/notification.service';
import { ServiceService } from '../../service/services/service.service';
import { SubcategoryService } from '../../service/services/subcategory.service';
import { ArticleService } from '../services/article.service';

@Component({
  selector: 'app-article-dialog',
  templateUrl: './article-dialog.component.html',
  styleUrls: ['./article-dialog.component.scss']
})
export class ArticleDialogComponent implements OnInit {

  filePath: string ="";

  services: IService[] =[]
  subcategories: ISubcategory[] = []
  listSvc=[]
  articleForm!: FormGroup;
  formData = new FormData()

  constructor(
    public dialogRef: MatDialogRef<ArticleDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private serviceSvc: ServiceService,
    private formB: FormBuilder,
    private notiSvc: NotificationService,
    private subcategorySvc: SubcategoryService,
    private articleSvc: ArticleService) {
      this.buildForm()
     }

    ngOnInit(): void {
      this.getServices()
      if(this.data?.element){
        this.articleForm.patchValue(this.data.element)
      }
    }
  
    submitArticle() {
      if( this.articleForm.invalid){
        this.notiSvc.openSnackBar("Algunos de los campos son invalidos", 3000)
        return
      }
      this.setServiceStore()
      if(this.data.element){
        this.articleSvc.updateArcitle(this.formData, this.data.element.id).subscribe(
          val => {
            console.log(val)
            this.notiSvc.openSnackBar("Articulo actualizado correctamente", 2000)
            this.dialogRef.close()
          },
          error => {
            console.log(error)
            this.notiSvc.openSnackBar("Error al actualizar el articulo", 2000)
          }
        )
      }else {
        this.articleSvc.createArticle(this.formData).subscribe(
          val => {
            console.log(val)
            this.notiSvc.openSnackBar("Articulo creado correctamente", 2000)
            this.dialogRef.close()
          },
          error => {
            console.log(error)
            this.notiSvc.openSnackBar("Error al crear el articulo", 2000)
          }
        )
      }
    }
    
    setServiceStore(): void {
      this.formData.append('title', this.articleForm.get('title')?.value)
      this.formData.append('content', this.articleForm.get('content')?.value)
      this.formData.append('author', this.articleForm.get('author')?.value)
      this.formData.append('picture', (<HTMLInputElement>document.getElementById('picture')).files[0])
      var array: IService[] = []
      for (var service of this.articleForm.get('services')?.value){
        array.push(
          {
            service_id: service.toString()  
          }
        )
      }
      this.formData.append('services', JSON.stringify(array))
      this.formData.forEach(function(value, key){
          console.log(key, value)
      })
    }
  
    buildForm(): void {
      this.articleForm = this.formB.group({
        title: ['', Validators.required],
        author: ['', Validators.required],
        content: ['', Validators.required],
        picture: ['', Validators.required],
        services: ['']
      })
    }
  
    onChange(e) {
      if (e.target.files){
        var reader = new FileReader()
        reader.readAsDataURL(e.target.files[0])
        reader.onload = (event : any) => {
          this.filePath = event.target.result;
        }
      }
    }

    
  getServices() :void {
    this.serviceSvc.showServices().subscribe((data) => {
      this.services = data.data
    })
  }

  getSubcategories(): void {
    this.subcategorySvc.showSubcategories().subscribe((data) => {
      this.subcategories = data.data
    })
  }
  
}

