import { animate, state, style, transition, trigger } from '@angular/animations';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSelect } from '@angular/material/select';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { IArticle } from 'src/app/shared/interfaces/i-article';
import { ArticleDialogComponent } from './article-dialog/article-dialog.component';
import { ArticleService } from './services/article.service';

@Component({
  selector: 'app-article',
  templateUrl: './article.component.html',
  styleUrls: ['./article.component.scss'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})

export class ArticleComponent implements OnInit {
  displayedColumns: string[] = ['id', 'title','author'];
  dataSource: MatTableDataSource<IArticle>;
  columnsToDisplay = ['id', 'title', 'author', 'see', 'edit'];
  expandedElement: IArticle | null;
  articles: IArticle[] = []

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(public dialog: MatDialog,
              private articleSvc: ArticleService) {

  }

  openDialog(edit: boolean, element?: IArticle): void {
    const dialogRef = this.dialog.open(ArticleDialogComponent, {
      disableClose: false,
      data: {edit, element}
    }).afterClosed().subscribe((data: any) => {
        this.getArticles()
    });
  }

  ngOnInit(): void {
    this.getArticles()
  }

  getArticles(): void {
    this.articleSvc.showArticles().subscribe((data) => {
      this.articles = data.data
      this.setData()
    })
  }


  setData(): void {
    this.dataSource = new MatTableDataSource();
    this.dataSource.data = this.articles;
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort
  }

  applyFilter(event: Event) {
      const filterValue = (event.target as HTMLInputElement).value;
      this.dataSource.filter = filterValue.trim().toLowerCase();

      if (this.dataSource.paginator) {
        this.dataSource.paginator.firstPage();
      }
    }

  changeActive() {
  }
}
