import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ArticleRes, IArticleAdd } from 'src/app/shared/interfaces/i-article';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ArticleService {
  constructor(private http: HttpClient) { }

  showArticles(): Observable<ArticleRes>{
    return this.http.get<ArticleRes>(`${environment.API_URL}/v1/articles`);
  }
  
  createArticle(data: FormData): Observable<IArticleAdd>{
    return this.http.post<IArticleAdd>(`${environment.API_URL}/v1/articles`, data);
  }

  updateArcitle(data: FormData, id: number): Observable<IArticleAdd>{
    return this.http.patch<IArticleAdd>(`${environment.API_URL}/v1/articles/`+ id, data);
  }
}
